.App {
  text-align: center;
  width: 100%vw;
}

.App-logo {
  -webkit-animation: App-logo-spin infinite 20s linear;
          animation: App-logo-spin infinite 20s linear;
  height: 80px;
}

.App-header {
  background-color: #222;
  height: 150px;
  padding: 20px;
  color: white;
}

html,body
{
    width: 100%;
    height: 100%;
    margin: 0px;
    padding: 0px;
    overflow-x: hidden; 
}

.Header {
  -webkit-animation: Header .8s forwards 0s ease-in;
          animation: Header .8s forwards 0s ease-in;
  color: whitesmoke;
  text-shadow: 1px 1px rgba(143, 153, 247, 0.253);
}

.poster {
  -webkit-animation: Poster .8s forwards 0s ease-out;
          animation: Poster .8s forwards 0s ease-out;
  -webkit-box-shadow: 6px 6px 5px 2px black;
          box-shadow: 6px 6px 5px 2px black;
  opacity: .8;
}

.App-intro {
  font-size: large; 
}

.navbar {
  display: inline-block;
}

body {
  background-image: url("../src/movietheaterblur.jpg");
  background-repeat: no-repeat;
  background-size: cover;
}

.search {
  margin-top: 5vh;
}

.searchButton {
  border-radius: 10px; 
}

#movie-display {
  color: whitesmoke;
  text-shadow: 2px 2px black;
}

.buttons {
  margin: 5px;
  padding: 8px;
  color: whitesmoke;
}

.buttons:hover {
  color: black;
  background-image: -webkit-radial-gradient(yellow, rgba(197, 197, 31, 0.596), rgba(168, 180, 62, 0.37));
  background-image: -o-radial-gradient(yellow, rgba(197, 197, 31, 0.596), rgba(168, 180, 62, 0.37));
  background-image: radial-gradient(yellow, rgba(197, 197, 31, 0.596), rgba(168, 180, 62, 0.37));
  border-radius: 50%;
  font-weight: bold;
}

.icon {
  font-size: 20px;
  margin: 5px;
  color: whitesmoke;
  text-shadow: 2px 2px black;
}

.home:hover ~ #home{
  opacity: 1;
}

.search:hover ~ #search{
  opacity: 1;
}

.userPage:hover ~ #userPage{
  opacity: 1;
}

.upcoming:hover ~ #upcoming{
  opacity: 1;
}

.logout:hover ~ #logout{
  opacity: 1;
}

.login:hover ~ #login{
  opacity: 1;
}

.register:hover ~ #register{
  opacity: 1;
}

.iconText {
  display: block;
  color: whitesmoke;
  margin-top: .5em;
  float: left;
  position: absolute;
  margin-left: 48vw;
  opacity: 0;
}

@-webkit-keyframes App-logo-spin {
  from { -webkit-transform: rotate(0deg); transform: rotate(0deg); }
  to { -webkit-transform: rotate(360deg); transform: rotate(360deg); }
}

@keyframes App-logo-spin {
  from { -webkit-transform: rotate(0deg); transform: rotate(0deg); }
  to { -webkit-transform: rotate(360deg); transform: rotate(360deg); }
}

@-webkit-keyframes Header{
  0%{
      -webkit-transform: rotate(0) translateY(-150%);
              transform: rotate(0) translateY(-150%);
      opacity: 0;
      }
  37%{
      -webkit-transform: rotate(0) translateY(-150%);
              transform: rotate(0) translateY(-150%);
      }
  100%{
      -webkit-transform: rotate(0) scale(2.5) translateY(20%);
              transform: rotate(0) scale(2.5) translateY(20%);
      opacity: 1;
      }
}

@keyframes Header{
  0%{
      -webkit-transform: rotate(0) translateY(-150%);
              transform: rotate(0) translateY(-150%);
      opacity: 0;
      }
  37%{
      -webkit-transform: rotate(0) translateY(-150%);
              transform: rotate(0) translateY(-150%);
      }
  100%{
      -webkit-transform: rotate(0) scale(2.5) translateY(20%);
              transform: rotate(0) scale(2.5) translateY(20%);
      opacity: 1;
      }
}

@-webkit-keyframes Poster{
  0%{
      -webkit-transform: rotate(0) translateX(-420%) skewX(50deg);
              transform: rotate(0) translateX(-420%) skewX(50deg);
      -webkit-transform-origin: 50% 50%;
              transform-origin: 50% 50%;
      opacity: 0.75;
      -webkit-box-shadow: 6px 6px 5px 2px black;
              box-shadow: 6px 6px 5px 2px black;
      }
  100%{
      -webkit-transform: rotate(0) skewX(0deg);
              transform: rotate(0) skewX(0deg);
      }
}

@keyframes Poster{
  0%{
      -webkit-transform: rotate(0) translateX(-420%) skewX(50deg);
              transform: rotate(0) translateX(-420%) skewX(50deg);
      -webkit-transform-origin: 50% 50%;
              transform-origin: 50% 50%;
      opacity: 0.75;
      -webkit-box-shadow: 6px 6px 5px 2px black;
              box-shadow: 6px 6px 5px 2px black;
      }
  100%{
      -webkit-transform: rotate(0) skewX(0deg);
              transform: rotate(0) skewX(0deg);
      }
}



